body {
  margin: 0;
  font-family: 'Satoshi', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

title {
  font-family: 'Raleway', sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@tailwind base;
@tailwind components;
@tailwind utilities;

.App {
  background-color: #f8f3ed;
  padding: 1rem 2rem;
  min-height: 100vh;
}

.App-logo {
  height: 10vmin;
  margin-top: 2rem;
  pointer-events: none;
}

.App-header {
  display: flex;
  grid-gap: 1rem;
  gap: 1rem;
  font-size: calc(25px + 2vmin);
  font-weight: 900;
  font-family: 'Raleway';
  color: #212529;
}

.App-link {
  color: #61dafb;
}

.btn {
  border: none;
  background: none;
  font-family: 'Satoshi';
  font-weight: bold;
  font-size: medium;
  cursor: pointer;
}

.btn:hover {
  color: #f1889f;
}

.buttons {
  width: 30rem;
  grid-gap: 14.5rem;
  gap: 14.5rem;
  display: flex;
  position: absolute;
  bottom: 50px;
  right: 110px;
}

